<template>
<div class="container">
    <div class="box">
        <div style="width: 100%; height: 100%;">
            <Line
            v-if="XyLoaded"
            id="my-chart-id"
            :options="XyChartOotions"
            :data="XyChartData"
            />
        </div>
    </div>
    <div class="box">
        <div style="width: 100%; height: 100%;">
            <Line
            v-if="GpuLoaded"
            id="my-chart-id"
            :options="GpuChartOptions"
            :data="GpuChartData"
            />
        </div>
    </div>
    <div class="box"></div>
    <div class="box"></div>
</div>
</template>

<script>
import { Line } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, CategoryScale, LinearScale, PointElement, LineElement } from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, PointElement, LineElement, CategoryScale, LinearScale)

export default {
    name: 'LineChart',
    components: { Line },
    data: () => ({
        XyChartData: null,
        GpuChartData: null,
        XyLoaded: false,
        GpuLoaded: false,
        intervals: [],
        XyChartOotions: {
            responsive: true,
            beginAtZero: true,
        },
        GpuChartOptions: {
            scales: {
                y: {
                    beginAtZero: true
                }
            },
            responsive: true,
            beginAtZero: true,

        },

        XyDataOptions: {
            label: 'XY chart',
            labelsKey: 'x_value',
            dataKey: 'y_value',
            borderColor: 'rgb(97, 193, 190)',
            maxDataPoints: 30
        },
        GpuDataOptions: {
            label: 'GPU chart',
            labelsKey: 'hms_time',
            dataKey: 'utilz_gpu',
            borderColor: 'rgb(231, 213, 232)',
            maxDataPoints: 30
        }
    }),

    methods: {
        async fetchLatestData(url, chartData, loaded, dataOptions) {
            try {
                const response = await fetch(url);
                let apiData = await response.json();
                this.updateChartData(apiData, chartData, dataOptions);
                this[loaded] = true;
            }
            catch(e) {
                console.log(e);
            }
        },

        updateChartData(apiData, chartData, dataOptions) {
            const maxDataPoints = dataOptions.maxDataPoints;
            if (apiData.length > maxDataPoints) {
                apiData = this.downsampleData(apiData, maxDataPoints);
            }
            this[chartData] = {
                labels: apiData.map(item => item[dataOptions.labelsKey]),
                datasets: [{
                    label: dataOptions.label,
                    data: apiData.map(item => item[dataOptions.dataKey]),
                    borderColor: dataOptions.borderColor
                }]
            }
        },

        downsampleData(data, maxPoints) {
            const sampleRate = Math.ceil(data.length / maxPoints);
            return data.filter((_, index) => index % sampleRate == 0);
        },
    },


    async mounted() {
        console.log("mounted");
        this.loaded = false;
        this.fetchLatestData('/api/v1/xy_graph/', 'XyChartData', 'XyLoaded', this.XyDataOptions);
        this.fetchLatestData('/api/v1/gpu/', 'GpuChartData', 'GpuLoaded', this.GpuDataOptions);
        this.intervals.push(setInterval(() => this.fetchLatestData('/api/v1/xy_graph/', 'XyChartData', 'XyLoaded', this.XyDataOptions), 5000));
        this.intervals.push(setInterval(() => this.fetchLatestData('/api/v1/gpu/', 'GpuChartData', 'GpuLoaded', this.GpuDataOptions), 5000));
    },
    unmounted() {
        this.intervals.forEach(clearInterval);
    }
}
</script>

<style>
.container {
    display: flex;
    flex-wrap: wrap;
    height: calc(100vh - 50px);
    overflow: hidden;
}

.box {
    flex: 50%;
    box-sizing: border-box;
    padding: 10px;
    height: 50%;
    border: 1px solid #ddd;
}
</style>